import React from 'react';
import classes from './Cv.module.scss';
import cvPhoto from '../../assets/photos/cvPhoto.webp';
import phoneIcon from '../../assets/icons/phone.svg';
import emailIcon from '../../assets/icons/mail.svg';
import telegramIcon from '../../assets/icons/brand-telegram.svg';
import githubIcon from '../../assets/icons/brand-github.svg';
import linkedinIcon from '../../assets/icons/brand-linkedin.svg';
import Card from './Cards/Card';
import PROJECTS from '../../constants/cvProjects';
import SKILLS from '../../constants/skills';

const Cv = () => {
    return (
        <section className={classes.cvContainer}>
            <div className={classes.cvWrapper}>
                <div className={classes.keyInfoSection}>
                    <div className={classes.imageContainer}>
                        <img src={cvPhoto} alt={'cv placeholder'} />
                    </div>
                    <div className={classes.personalInfoContainer}>
                        <h2 className={classes.fullName}>Daniel Siarha</h2>
                        <p>Date of birth: 11.07.2003</p>

                        <div className={classes.contactInfo}>
                            <h3>Communication</h3>
                            <div className={classes.phoneContainer}>
                                <img src={phoneIcon} alt='phone icon' />
                                <a href='tel:+375291731104'>+375 (29) 173-11-04</a>
                            </div>
                            <div className={classes.emailContainer}>
                                <img src={emailIcon} alt='email icon' />
                                <a href='mailto:daniil.serga@gmail.com'>daniil.serga@gmail.com</a>
                            </div>
                            <div className={classes.telegramContainer}>
                                <img src={telegramIcon} alt='telegram icon' />
                                <a href='https://t.me/daniilsergx'>https://t.me/daniilsergx</a>
                            </div>
                            <div className={classes.githubContainer}>
                                <img src={githubIcon} alt='github icon' />
                                <a href='https://github.com/DaniilSerga'>https://github.com/DaniilSerga</a>
                            </div>
                            <div className={classes.linkedinContainer}>
                                <img src={linkedinIcon} alt='linkedIn icon' />
                                <a href='https://www.linkedin.com/in/daniil-serga-7499b7242/'>https://www.linkedin.com/in/daniil-serga-7499b7242/</a>
                            </div>
                        </div>
                    </div>
                </div>

                <div className={classes.infoSection}>
                    <div className={classes.positionSection}>
                        <h3 className={classes.subtitle}>Preffered positions</h3>
                        <p>Specializations:</p>
                        <ul>
                            <li>
                                <p>Frontend developer</p>
                            </li>
                            <li>
                                <p>Backend .NET developer</p>
                            </li>
                        </ul>
                        <p>Work schedule: rotation based work, remote working, flexible schedule, full day, shift schedule</p>
                        <p>Desired travel time to work: any</p>
                    </div>
                    <div className={classes.experienceSection}>
                        <h3 className={classes.subtitle}>Work experience</h3>
                        <div className={classes.projectsWrapper}>
                            <div className={classes.projectsContainer}>
                                {
                                    PROJECTS.map(project =>
                                        <Card key={project.id} project={project} />
                                    )
                                }
                            </div>
                        </div>
                    </div>
                    <div className={classes.educationSection}>
                        <h3 className={classes.subtitle}>Education</h3>
                        <table className={classes.educationPlacesTable}>
                            <tbody>

                                <tr>
                                    <td>
                                        <p className={classes.date}>2020-2024</p>
                                    </td>
                                    <td>
                                        <p className={classes.organizationName}>Polessky State University</p>
                                        <p>Qualification: “Software engineer-economist"</p>
                                        <p>Speciality: “Information Systems and Technologies”</p>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <p className={classes.date}>2021</p>
                                    </td>
                                    <td>
                                        <p className={classes.organizationName}>Epam Training Center</p>
                                        <p>Training program: .NET development</p>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <p className={classes.date}>2022-2023</p>
                                    </td>
                                    <td>
                                        <p className={classes.organizationName}>PolessUP laboratory</p>
                                        <p>Position: Backend .NET developer</p>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <p className={classes.date}>2023</p>
                                    </td>
                                    <td>
                                        <p className={classes.organizationName}>The Rolling Scopes</p>
                                        <p>Preparatory course "JavaScript/Front-end. Stage 0"</p>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <p className={classes.date}>2023-present</p>
                                    </td>
                                    <td>
                                        <p className={classes.organizationName}>YOUX Systems</p>
                                        <p>Position: Frontend intern</p>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className={classes.skillsSection}>
                        <h3 className={classes.subtitle}>Key skills</h3>
                        <table className={classes.educationPlacesTable}>
                            <tbody>
                                <tr>
                                    <td>
                                        <p className={classes.skillName}>Hard skills</p>
                                    </td>
                                    <td>
                                        <div className={classes.skillsContainer}>
                                            {
                                                SKILLS.hardSkills.map((skill, index) =>
                                                    <p key={index} className={classes.skill}>{skill}</p>    
                                                )
                                            }
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <p className={classes.skillName}>Languages</p>
                                    </td>
                                    <td>
                                        <div className={classes.languagesContainer}>
                                            {
                                                SKILLS.languages.map((language, index) =>
                                                    <div key={index} className={classes.languageContainer}>
                                                        <p>{language.name}</p>    
                                                        <p>–</p>
                                                        <p>{language.level}</p>    
                                                    </div>
                                                )
                                            }
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Cv;